.preloader-mini{height:0;width:100%;max-width:700px;position:fixed;bottom:25px;z-index:1}.preloader-mini:before{content:'';height:3px;width:20px;position:absolute;left:0;background-color:var(--blue);-webkit-animation:preloaderAnim 3s infinite linear;animation:preloaderAnim 3s infinite linear}@media screen and (max-width: 740px){.preloader-mini{max-width:100%;left:0%}}@-webkit-keyframes preloaderAnim{0%{left:0px}100%{left:calc(100% - 20px)}}@keyframes preloaderAnim{0%{left:0px}100%{left:calc(100% - 20px)}}

.books-list{padding:0 10px;width:100%;max-width:720px}.books-list__container{display:grid;grid-template-columns:repeat(auto-fill, 100px);grid-gap:1.1rem;justify-content:space-between;margin-top:10px}@media screen and (max-width: 740px){.books-list{max-width:640px}}@media screen and (max-width: 640px){.books-list{max-width:540px}}@media screen and (max-width: 480px){.books-list{max-width:420px}}@media screen and (max-width: 364px){.books-list{max-width:260px}}@media screen and (max-width: 250px){.books-list{max-width:220px}}

.books-list-info{height:25px;width:100%;max-width:700px;box-shadow:0px -11px 20px -10px rgba(141,141,141,0.5);position:fixed;display:flex;align-items:center;justify-content:center;flex-wrap:nowrap;bottom:0}.books-list-info__title{font-size:small;color:#9b9b9b}@media screen and (max-width: 740px){.books-list-info{max-width:100%;left:0}}

.scroll{height:calc(100vh - 73px);margin:0 3px;width:100vw;display:flex;justify-content:center;overflow:overlay;position:relative}

.book-card{padding:0px 0px;width:100px;height:200px;display:flex;flex-direction:column;justify-content:end;grid-gap:5px;gap:5px;cursor:pointer}.book-card:hover .book-card__img{-webkit-filter:brightness(88%);filter:brightness(88%)}.book-card:hover .book-card__title{color:var(--book-card__title--hover)}.book-card:hover .book-card__author{color:var(--book-card__author--hover)}@media screen and (max-width: 364px){.book-card{width:35vw;height:auto}}@media screen and (max-width: 250px){.book-card{width:86vw;height:auto}}.book-card__img{pointer-events:none;height:auto;width:100%;border-radius:4px;overflow:auto}.book-card__description{pointer-events:none;width:100%;height:60px}.book-card__title{padding:0px 0px 0px 0px;margin:0px 0px 2px 0px;pointer-events:none;max-height:28px;overflow:hidden;font-size:12px;font-weight:400;word-break:break-all;color:var(--book-card__title)}.book-card__author{padding:0px 0px 0px 0px;margin:0px 0px 0px 0px;pointer-events:none;max-height:28px;overflow:hidden;font-size:12px;color:var(--book-card__author)}

.error{display:flex;flex-direction:column;flex-wrap:nowrap;align-items:center}.error__title{text-align:center;margin:10px;font-size:25px}.error__message{text-align:center;font-size:15px}

.theme{flex-shrink:0;border-radius:5px;border:1px solid var(--theme-button-border);cursor:pointer;overflow:hidden}.theme:hover{border:1px solid var(--theme-button-border-hover)}.theme__img{width:32px;height:32px;pointer-events:none;-webkit-user-select:none;-ms-user-select:none;user-select:none}.theme__icon{width:32px;height:32px;display:flex;justify-content:flex-start;transition-property:all;transition-duration:0.3s;transition-timing-function:linear}.theme__icon.sun{-webkit-transform:translate(0%);transform:translate(0%)}.theme__icon.moon{-webkit-transform:translate(-100%);transform:translate(-100%)}

.logo__img{height:48px;margin:36px 0 0 0;pointer-events:none;-webkit-user-select:none;-ms-user-select:none;user-select:none}

.zero-book{display:flex;flex-direction:column;flex-wrap:nowrap;align-items:center}.zero-book__title{text-align:center;margin:10px;font-size:25px}.zero-book__text{text-align:center;font-size:15px}

.header{width:100%;height:3rem;position:fixed;background-color:var(--header);z-index:1}.header__container{max-width:720px;height:100%;margin:0 auto;display:flex;grid-gap:10px;padding:0px 12px;align-items:center;justify-content:space-between}.header__title{font-weight:300;font-size:20px;-webkit-user-select:none;-ms-user-select:none;user-select:none;flex-shrink:0;color:var(--header-title)}@media screen and (max-width: 600px){.header__title{display:none}}

.main{padding-top:3rem}.main__container{height:100%;display:flex}

